import React, { useState } from 'react';
import { Tag, Input } from 'antd'
import { PlusOutlined } from '@ant-design/icons'

import styles from './index.module.less'
const TagList = (props) => {
    const { tags = [] } = props;
    const [viseble, setViseble] = useState(false);
    const [newTags, setNewTags] = useState(tags);
    const [inputValue, setInputValue] = useState('');
    const showInput = () => {
        setViseble(true);
    }
    const changeInputValue = (e) => {
        setInputValue(e.target.value);
    };
    const changeNewTags = () => {
        let temp = [...newTags];
        if (temp.find((value) => value.label === inputValue) === undefined) {
            setNewTags([...temp, { key: `${temp.length}`, label: inputValue }]);
        }
        setInputValue('');
        setViseble(false);
    };
    return <div className={styles.tagList}>
        {newTags.map((value) => (
            <Tag className={styles.tag} key={value.key}>{value.label}</Tag>
        ))}
        {!viseble && <Tag style={{ borderStyle: 'dashed' }} onClick={showInput}>
            <PlusOutlined />
        </Tag>}
        {viseble && <Tag style={{ borderStyle: 'hidden' }}>
            <Input size="small" value={inputValue} style={{ width: 78 }} onChange={changeInputValue} onBlur={changeNewTags} onPressEnter={changeNewTags} />
        </Tag>}
    </div>
}

export default TagList;