import React from 'react';
import { List, Tag } from 'antd'
import { StarOutlined, LikeOutlined, MessageFilled } from '@ant-design/icons'
import styles from './index.module.less';
import ArticlesContent from '../ArticlesConten';

const IconText = ({ icon, text }) => (
    <span>{icon} {text}</span>
);

const Articles = ({ list = [] }) => {
    return <div>
        <List
            className={styles.articlesList}
            dataSource={list}
            rowKey='id'
            size="large"
            itemLayout="vertical"
            renderItem={(item) =>
                <List.Item
                    actions={[
                        <IconText icon={<StarOutlined />} text={item.star} />,
                        <IconText icon={<LikeOutlined />} text={item.like} />,
                        <IconText icon={<MessageFilled />} text={item.message} />,
                    ]}>
                    <List.Item.Meta title={item.title} description={
                        <span>
                            <Tag>Ant Design</Tag>
                            <Tag>设计语言</Tag>
                            <Tag>蚂蚁金服</Tag>
                        </span>
                    } />
                    <ArticlesContent data={item} />
                </List.Item>
            }

        />
    </div>;
}

export default Articles;