import * as api from '../api/profile'
import { message as messageNotice } from 'antd';
import { GET_PROFILE } from '../constants/account'

export function getAccountCenter(payload = {}) {
    return async (dispatch) => {
        const result = await api.getAccountCenter(payload);
        const { code, message, data } = result;
        if (code === 0) {
            dispatch({
                type: GET_PROFILE,
                payload: data
            });
        } else {
            messageNotice.error(message);
        }
    }
}

