import React, { useState, useEffect } from 'react'
import { Row, Col, Card, Divider, Avatar } from 'antd'
import { ContactsOutlined, ClusterOutlined, HomeOutlined } from '@ant-design/icons'
import { Link } from 'react-router-dom'
import styles from './index.module.less'
import Projects from './components/Projects'
import Applactions from './components/Applactions'
import Articles from './components/Articles'
import { currentUser, fakeList } from './data'
import TagList from './components/Tags'
import { useDispatch, useMappedState } from 'redux-react-hook'
import { getAccountCenter } from '../../actions/profile'
const tabLists = [
    {
        key: 'articles',
        tab: (
            <span>文章<span>(8)</span></span>
        ),
    },
    {
        key: 'applactions',
        tab: (
            <span>应用<span>(15)</span></span>
        ),
    },
    {
        key: 'projects',
        tab: (
            <span>项目<span>(20)</span></span>
        ),
    },
];

const articleList = fakeList(10);

const ApplactionsList = fakeList(10);

const ProjectsList = fakeList(10);

const mapState = state=>state.profile;

const renderUserInfo = () => (
    <div className={styles.detail}>
        <p>
            <ContactsOutlined className={styles.userInfoIcons} />{currentUser.title}
        </p>
        <p>
            <ClusterOutlined className={styles.userInfoIcons} />{currentUser.group}
        </p>
        <p>
            <HomeOutlined className={styles.userInfoIcons} />{(currentUser.geographic || { province: { lable: '' } }).province.label}{(currentUser.geographic || { city: { lable: '' } }).city.label}
        </p>
        <Divider dashed />
        <TagList tags={currentUser.tags} />
        <Divider dashed />
        <div className={styles.team}>
            <div className={styles.teamTitle}>团队</div>
            <Row gutter={24}>
                {currentUser.notice && currentUser.notice.map((value) => (
                    <Col lg={24} xl={12} key={value.id}>
                        <Link to={value.href}><Avatar size="small" src={value.logo} />{value.member}</Link>
                    </Col>
                ))}
            </Row>
        </div>
    </div>
);

const Home = () => {
    const [tabListKey, setTabListKey] = useState('projects');
    const onChangeTabListKey = (key) => {
        setTabListKey(key);
    }
    const { user = {} } = useMappedState(mapState);
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getAccountCenter());
    }, [dispatch]);


    return (
        <div className={styles.container}>
            <Row gutter={24}>
                <Col lg={7} md={24} style={{ marginBottom: 24 }}>
                    <Card bordered={false} >
                        <div className={styles.avatarHolder}>
                            <img alt="" src={currentUser.avatar} />
                            <p className={styles.name}>{user.username}</p>
                            <p className={styles.signature}>{currentUser.signature}</p>
                        </div>
                        {renderUserInfo(currentUser)}
                    </Card>
                </Col>
                <Col lg={17} md={24}>
                    <Card
                        bordered={false}
                        tabList={tabLists}
                        defaultActiveTabKey='projects'
                        onTabChange={onChangeTabListKey}
                    >
                        {tabListKey === 'articles' && <Articles list={articleList} />}
                        {tabListKey === 'applactions' && <Applactions list={ApplactionsList} />}
                        {tabListKey === 'projects' && <Projects list={ProjectsList} />}
                    </Card>
                </Col>
            </Row>
        </div>
    );
};

export default Home;