import React from 'react';
import { List, Avatar, Card, Tooltip, Dropdown, Menu } from 'antd';
import { DownloadOutlined, EditOutlined, ShareAltOutlined, EllipsisOutlined } from '@ant-design/icons'
import numeral from 'numeral'
import styles from './index.module.less'
const itemMenus = () => (
    <Menu>
        <Menu.Item ><a target="_blank" rel="noopener noreferer" href='www.baidu.com'>1 first MenuItem</a></Menu.Item>
        <Menu.Item ><a target="_blank" rel="noopener noreferer" href='www.baidu.com'>2 first MenuItem</a></Menu.Item>
        <Menu.Item ><a target="_blank" rel="noopener noreferer" href='www.baidu.com'>3 first MenuItem</a></Menu.Item>
    </Menu>
);

const CardInfo = ({ newUser, activeUser }) => {
    return <div className={styles.cardInfo}>
        <div>
            <p>活跃用户</p>
            <p>{newUser}</p>
        </div>
        <div>
            <p>新增用户</p>
            <p>{activeUser}</p>
        </div>
    </div>;
};

function formatWan(value) {
    const temp = value * 1;
    if (!temp || Number.isNaN(temp)) return '';
    return <span>{Math.floor(temp / 10000)}
        <span>万</span>
    </span>

};

const Applactions = ({ list }) => {
    return <div>
        <List
            className={styles.applactionsCardList}
            dataSource={list}
            rowKey='id'
            size="large"
            grid={{ gutter: 24, xxl: 3, xl: 2, lg: 2, md: 2, sm: 1 }}
            renderItem={(item) =>
                <List.Item
                    key={item.id}
                >
                    <Card
                        hoverable
                        actions={[
                            <Tooltip key='download' title='下载' ><DownloadOutlined /></Tooltip>,
                            <Tooltip key='edit' title='编辑'><EditOutlined /></Tooltip>,
                            <Tooltip key='share' title='分享'><ShareAltOutlined /></Tooltip>,
                            <Dropdown key='dropdown' overlay={itemMenus} >
                                <EllipsisOutlined />
                            </Dropdown>,
                        ]}
                    >
                        <Card.Meta title={item.title} avatar={<Avatar src={item.avatar} size="small" />} />
                        <CardInfo newUser={formatWan(item.activeUser)} activeUser={numeral(item.newUser).format('0,0')} />
                    </Card>
                </List.Item>
            }

        />
    </div>;
}

export default Applactions;